<template>
    <div>
        <!-- 主要导航栏 -->
        <div class="w">
            <div class="fr">
                <div class="shortcut_box fl" @mousemove="options_mousemove">
                    <p>课程建设与分享</p>
                </div>
                <div class="shortcut_box fl" @mousemove="options_mousemove" @click="goNJFUTeachingSearch">
                    <p class="orangeCursor">协同教学研究</p>
                </div>
                <div class="shortcut_box fl" @mousemove="options_mousemove">
                    <p>教学资源共建共享</p>
                </div>
                <div class="shortcut_box fl" @mousemove="options_mousemove">
                    <p>协同机制</p>
                </div>
                <div class="shortcut_box fl" @mousemove="options_mousemove" @click="goNJFUSpace()">
                    <p class="orangeCursor">欢迎回来, {{ TI_name }}</p>
                </div>
            </div>
        </div>

        <!-- 透明灰色背景 -->
        <div class="shortcut_box_detailed_background opacity6" v-show="detail_index == 1"></div>

        <!-- 详细导航栏 -->
        <div class="w" v-show="detail_index == 1">
            <div class="shortcut_box_detailed">
                <ul class="fl">
                    <li v-for="(detail, index) in navigationDetails[0]">
                        <p class="orangeCursor" @click="goNJFUFileShow(index, detail)">{{ detail }}</p>
                    </li>
                </ul>
                <ul class="fl">
                    <li v-for="(detail, index) in navigationDetails[1]">
                        <p class="orangeCursor" @click="goNJFUMesRelease(index, detail)">{{ detail }}</p>
                    </li>
                </ul>
                <ul class="fl">
                    <li v-for="(detail, index) in navigationDetails[2]">
                        <p class="orangeCursor" @click="goNJFUCourseResources(index, detail)">{{ detail }}</p>
                    </li>
                </ul>
                <ul class="fl">
                    <li v-for="(detail, index) in navigationDetails[3]">
                        <p>{{ detail }}</p>
                    </li>
                </ul>
                <ul class=" fl">
                    <li @click="logout()">
                        <p class="orangeCursor">退出登录</p>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'HeadNavigation',
    data() {
        return {
            detail_index: 0,
            navigationDetails: [
                ['课程体系', '课程大纲', '教学环节设计', '优秀教案展示'],
                ['信息发布', '教学项目立项建议', '研究进展追踪', '研究项目的课题总结', '成果展示', '汇总分享'],
                ['课程1', '课程2', '课程3'],
                ['视频会议', '语音会议', '公共讨论区', '协同信息编辑']
            ]
        }
    },
    computed: {
        TI_name() {
            return this.$store.state.userMesOptions.TI_name
        }
    },
    methods: {
        // 详细导航栏的显示与隐藏
        options_mousemove: function () {
            this.detail_index = 1;
            document.onmousemove = (event) => {
                if (event.pageY > 551) {
                    scrolly1 = 0;
                    scrolly2 = 0;
                    document.onmousemove = '';
                    document.onmouseleave = '';
                    document.onwheel = '';
                    this.detail_index = 0;
                }
            }
            document.onmouseleave = () => {
                scrolly1 = 0;
                scrolly2 = 0;
                document.onmousemove = '';
                document.onmouseleave = '';
                document.onwheel = '';
                this.detail_index = 0;
            }
            var scrolly1 = 0;
            var scrolly2 = 0;
            document.onwheel = (event) => {
                scrolly1 = window.scrollY;
                setTimeout(function () {
                    if (scrolly1 == 0)
                        scrolly2 = window.scrollY;
                    if (event.pageY + scrolly2 > 551) {
                        scrolly1 = 0;
                        scrolly2 = 0;
                        document.onmousemove = '';
                        document.onmouseleave = '';
                        document.onwheel = '';
                        this.detail_index = 0;
                    }
                }, 100);
            }
        },
        //退出登录
        logout: function () {
            this.$store.dispatch('userMesOptions/logout')
        },
        goNJFUFileShow(fileShowID, fileShowTitle) {
            this.$router.push({ name: 'FileShow', query: { fileShowID, fileShowTitle } }, _ => { })
        },
        goNJFUCourseResources(CourseResourcesID, CourseResourcesTitle) {
            this.$router.push({ name: 'CourseResources', query: { CourseResourcesID, CourseResourcesTitle } }, _ => { })
        },
        goNJFUSpace() {
            this.$router.push({ name: 'Space' }, _ => { })
        },
        goNJFUTeachingSearch() {
            this.$router.push({ name: 'TeachingResearch' }, _ => { })
        },
        goNJFUMesRelease(MesReleaseID, MesReleaseTitle) {
            this.$router.push({ name: 'MesRelease', query: { MesReleaseID, MesReleaseTitle } }, _ => { })
        }
    }
}
</script>

<style scoped>
.shortcut_box {
    height: 82px;
    width: 200px;
    color: #ffffff;
}

.shortcut_box p {
    width: 100%;
    font-size: 20px;
    text-align: center;
    line-height: 40px;
}

.shortcut_box_detailed_background {
    position: absolute;
    left: 0;
    top: 72px;
    width: 100%;
    min-width: 1400px;
    height: 420px;
    z-index: 2048;
    background-color: #222;
}

.shortcut_box_detailed {
    position: absolute;
    top: 72px;
    width: 1000px;
    margin-left: 400px;
    z-index: 4096;
}

.shortcut_box_detailed ul {
    width: 200px;
}

.shortcut_box_detailed ul li {
    height: 40px;
    color: white;
    text-align: center;
    line-height: 40px;
    font-size: 20px;
}
</style>